<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import dayjs from 'dayjs';
export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        date: '',
      },
      date: '',
      loading: false,
      tableData: [],
      count: 0,
      page_curren: 1,
    }
  },
  // watch: {
  //   date(val) {
  //     if(val) {
  //       this.searchs.date = dayjs(val).format('YYYY-MM-DD')
  //     } else {
  //       this.searchs.date = ''
  //     }
  //   }
  // },
  // filters: {
  //   dateFormat(time) {
  //     return moment(parseInt(time) * 1000).format('YYYY-MM-DD');
  //   },
  // },
  mounted() {
    this.FetchList()
  },
  methods: {
    dateChoose(val){
      if(val) {
        this.searchs.date = dayjs(val).format('YYYY-MM-DD')
      } else {
        this.searchs.date = ''
      }
    },
    FetchList() {
      this.loading = true
      Fetch('/api/api/downsearch', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.tableData = r.data.list
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
        })
        .catch(res => {
          Message.error(res.msg)
        })
      this.loading = false
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    // editItem(row) {
    //   this.$router.push('/article/edit/' + row.id)
    // },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            // instance.confirmButtonLoading = true
            // Fetch('/article/art/remove', { id: row.id })
